.footer {
    margin-top: 50px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.footerItems h2 {
    position: relative;
    top: -20px;
    font-family: cursive;
    font-style: italic;
}

@media only screen and (max-width: 1024px) {
    .footer {
        height: 70px
    }
    .footerItems {
        display: none;
    }
}